export const setupSticky = (selector, height = 0) => {
  const options = {
    rootMargin: `-${height}px 0px 0px 0px`,
    threshold: [0.0, 0.5, 1.0],
  };

  const observer = new IntersectionObserver((entries) => {
    entries.forEach((entry) => {
      if (!entry.isIntersecting) {
        entry.target.parentElement.querySelector(".sticky").classList.add("stuck");
      } else {
        entry.target.parentElement.querySelector(".sticky").classList.remove("stuck");
      }
    });
  }, options);

  const targets = document.querySelectorAll(selector);

  targets.forEach((target) => {
    const separator = target.querySelector(".separator") || document.createElement("div");
    separator.classList.add("separator");
    target.insertBefore(separator, target.firstChild);

    target.querySelectorAll(".sticky").forEach((i) => {
      i.style.top = `${height}px`;
    });

    observer.observe(separator);
  });

  return () => {
    targets.forEach((target) => {
      observer.unobserve(target.querySelector(".separator"));
      observer.disconnect();
    });
  };
};

export const setupHeaders = () => {
  const header = document.querySelector("header.main");
  const { height } = header.getBoundingClientRect();

  return setupSticky(".sticky-container", height);
};
